import { Dialog } from "vant";
import {
    getProductInfo,
    getProductShopInfo,
    getFlashSaleDetail,
    remindUser,
    getToPay,
    getFlashSaleUrl,addToCart, addToCollection, delectCollection, ifCollection, getCartNum,
} from "../../services/productInfoService";
import { mapActions, mapState} from "vuex";
import Preview from "@/components/preview/Preview.vue";
import detailCountDown from "../../components/flashSaleCountDown/detailCountDown"
import { ImagePreview} from 'vant';
import {storage} from '../../utils/storage';

export const flashSaleGoods = {
    name: "flashSaleGoods",
    data() {
        return {
            lists: {
                spuPicUrlList:[]
            },
            flashSaleData: {}, //商品秒杀信息
            shopInfo:{},//店铺信息
            skuList: [],
            skuNum: [], //购物车角标
            collectCode: [],
            cutOffTimeStr: [],
            goodsNum: 0, //购买数量
            icon: {
                collection: require("@/assets/images/mall/navbar_icon_collection_black@2x.png"),
                afterCollectionBg: require("@/assets/images/mall/nav_icon_collect_sel@2x.png"),
                collectionBg: require("@/assets/images/mall/navbar_icon_collection_blackBg@2x.png"),
                afterCollection: require("@/assets/images/mall/navbar_icon_collection_sel.png"),
                shoppingCartBg: require("@/assets/images/mall/nav_icon_cart.png"),
                shoppingCart: require("@/assets/images/mall/mall_icon_shoppingcart_black.png"),
                arrowBg: require("@/assets/images/mall/navbar_icon_arrow_blackBg@2x.png"),
                arrow: require("@/assets/images/common/nav_return_black.png"),
                share: require("@/assets/images/mall/mall_icon_share_gray.png"),
                topClick: require("@/assets/images/mall/redLine.png"),
                callShop: require("@/assets/images/mall/mall_icon_call.png"),
                shop: require("@/assets/images/mall/mall_icon_shop.png"),
                evaluate: require("@/assets/images/mall/mall_icon_more.png"),
                like: require("@/assets/images/common/icon_evaluate_red.png"),
                noLike: require("@/assets/images/common/icon_evaluate_gray.png"),
                defaultXWJPic:require("@/assets/images/default/default_picture.png"),
                noNetworkImg:require("@/assets/images/home/notice/img_net@2x.png"),
                shareChart:require("@/assets/images/common/nav_icon_share_wechat.png"),
                shareFriends:require("@/assets/images/common/nav_icon_share_moments.png"),
            },
            show: {
                normalTitle: true, //透明吸顶
                activeTitle: false, //不透明吸顶
                titShow: 1, //顶部标题的详情和评价点击切换
                weChartShare: false, //微信分享
                callShop: false,
                firstEvaluate: true,
                showDialog: false, //显示遮罩层
                goodsDown: false, //已下架弹窗
                saleOut: false, //补货中弹窗
                remindSale: false, //订阅弹窗
                bannerChangeBool:false,
                networkError:true,//网络异常请求
                afterCollect: false,
                beforeCollect: true,
            },
            evalInfo: [],
            num: 1, //购买数量
            detailDescribe: [],
            ownerPicUrl: require("@/assets/images/default/user_default_avata.png"),
            time: 1000, //倒计时
            activitySpuId: this.$route.query.activitySpuId,
            // status: this.$route.query.status, //秒杀商品状态：待开始 ：0 秒杀中：1 已下架
            status:-1,//商品状态 0:未开始 1:进行中 2:已结束 3:已暂停(app显示售罄) 4:补货中 ,
            remind:'开抢时提醒我',
            isShowTopBtn:false, //是否显示置顶按钮
            setTopHeight:{
                'padding-top':this.$stbHeight+'px'
            },
            images: [],// 图片预览属性
            productImgs:[],
            hasStoragePermission:false,
            isShowPreview:false,
            isShowPlaceHolder:false,
        };
    },
    components: {
        "Preview": Preview,
        detailCountDown
    },
    methods: {
        //置顶按钮
        goTopBtn(){
            this.$refs.goodsPageScroll.scrollTop = 0;
        },
        bannerChange(){
            this.show.bannerChangeBool = true;
            setTimeout(()=>{
                this.show.bannerChangeBool = false;
            },500)
        },
        //商品图片缓存完后替换商品图，优化显示效果
        productImgDownload() {
            let picNum=0;
            let that = this;
            this.lists.spuPicUrlList.map((item,i)=>{
                var img = new Image();
                img.onload = function(){
                    picNum +=1;
                    console.log("商品详情图片"+picNum);
                    console.log(that.lists.spuPicUrlList.length+"==========="+picNum);
                    if (that.lists.spuPicUrlList.length==picNum) {
                        console.log("==========="+picNum);
                        that.productImgs = that.lists.spuPicUrlList;
                    }
                };
                img.src = that.lists.spuPicUrlList[i];
            });
        },
        //查看商品大图
        bannerPreview(bannerImgArr,index){
            if(this.show.bannerChangeBool){
                return;
            }
            this.images = bannerImgArr;
            this.isShowPreview = true;
            this.$nextTick(()=>{
                this.$refs.myPreview.setIndex(index);
                this.$refs.myPreview.setIsShowPreview(true);
                this.$store.dispatch("setPreviewId", this.$refs.myPreview);
            })
        },
        preview(evalPicUrl, index){
            let srcArr = [];
            evalPicUrl.map((evalPicUrlItem)=>{
                srcArr.push(evalPicUrlItem.picUrl)
            })
            this.images = srcArr;
            this.isShowPreview = true;
            this.$nextTick(()=>{
                this.$refs.myPreview.setIndex(index);
                this.$refs.myPreview.setIsShowPreview(true);
                this.$store.dispatch("setPreviewId", this.$refs.myPreview);
            })
        },
        //通过spu获取商品信息
        init: function() {
            const data = {
                spuCode: this.spuCode
            };
            getProductInfo(data).then(response =>{
                if(response.resultCode=='0'){
                    this.lists = response.data;

                    if(this.lists.cutOffTimeStr&&this.lists.cutOffTimeStr.indexOf("<br/>")){
                        this.lists.cutOffTimeStr= this.lists.cutOffTimeStr.replace(/<br\/>/g,'\n')
                    }

                    this.productImgDownload();
                    this.show.networkError=false;
                    this.skuList = response.data.skuList[0];
                    this.evalInfo = response.data.evalInfo;
                    const eva = response.data.evalInfo;
                    if (eva == null) {
                        this.show.firstEvaluate = false;
                    } else {
                        console.log("有第一条评价");
                        this.show.firstEvaluate = true;
                    }
                    this.getShopInfo(this.lists.shopCode);
                }else{
                    Dialog.alert({
                        message:response.msg
                    }).then(() => {
                        this.$router.go(-1);//返回上一层
                    });
                }
            })
            // .catch(error => {
            //     this.show.networkError=true;
            //     this.$toast("网络异常，请稍后重试");
            //     this.productImgs=[this.icon.defaultXWJPic];
            // });
        },
        // 获取店铺信息
        getShopInfo(shopCode){
            console.log("店铺详情==========>")
            const data={
                shopCode:shopCode
            }
            getProductShopInfo(data).then(response=>{
                this.shopInfo=response.data;
            });
        },
        //通过activityId获取商品秒杀信息
        flashSaleDetail() {
            const data = {
                activitySpuId:this.activitySpuId,
                ownerCode:this.$userInfo.OWNER_CODE,
            };
            getFlashSaleDetail(data).then(response=> {
                console.log("提醒业主"+JSON.stringify(response));
                this.status=response.data.spuStatus;
                this.flashSaleData = response.data;
                //  this.status=0;
                if (this.status == 0) {
                    this.flashSaleData.time = (response.data.startTime - response.data.nowTime) * 1000;
                    console.log("获取到的时间"+this.flashSaleData.time)
                    this.remind=response.data.isSubsribe==0?'开抢时提醒我':'取消订阅';
                } else {
                    this.flashSaleData.time = (response.data.endTime - response.data.nowTime) * 1000;
                    console.log(1111111111+this.time);
                }
                console.log(1111111111+this.flashSaleData.spuSkPrice);
            }).catch(error => {
                this.show.networkError=true;
                this.$toast("网络异常，请稍后重试");
                this.productImgs=[this.icon.defaultXWJPic];
            });
        },
        // 秒杀结算
        toBuy() {
            const data={
                activitySpuId: this.activitySpuId,
                count: this.goodsNum
            }
            if(this.status!=1){
                return;
            }
            console.log("是否绑定房间"+this.$userInfo.ROOM_CODE)
            if(!this.$userInfo.ROOM_CODE) {
                Dialog.confirm({
                    message: "您未绑定房间，是否前往绑定？",
                    confirmButtonText: "前往"
                }).then(() => {
                    //跳转到绑定房产页面
                    this.$router.push("selectArea");
                }).catch(() => {
                });
                return;
            }
            // getToPay(data).then(response=>{
            //   if(response.resultCode==0){
            const settleAccountsList = [];
            settleAccountsList.push({
                shopName: this.lists.shopName,
                shopCode: this.lists.shopCode,
                shopTotal: this.goodsNum * this.lists.skuList[0].skuPrice, //数量需要改变
                skuList: [
                    {
                        spuCode: this.lists.skuList.spuCode,
                        spuName: this.lists.skuList.spuName,
                        skuNo: this.lists.skuList[0].skuNo,
                        attrValueContent: this.lists.skuList[0].attrValueContent,
                        skuPrice: this.flashSaleData.skuPrice,
                        skuCount: this.goodsNum, //数量
                        skuDelivery: 0,
                        skuTotal: this.goodsNum * this.lists.skuList[0].skuPrice,
                        skuPicUrl: this.lists.skuPicUrl,
                        activitySpuId:this.activitySpuId
                    }
                ]
            });
            const goodsData = {
                orderSource: "2",
                goodsList: settleAccountsList,
                num: Math.random() * 100
            };
            console.log("跳转传参："+JSON.stringify(goodsData));
            this.$router.push({
                name: "orderPayNew",
                params: {
                    'goodsData': goodsData
                }
            });
        },
        handleScroll: function() {
            //监听滚动设置吸顶格式
            const _this=this;
            _this.$nextTick(() => {
                let scrollTop = _this.$refs.goodsPageScroll.scrollTop;
                const changeTodetail =_this.$refs.detailChange.offsetTop-60;
                if(scrollTop >this.$stbHeight){
                    _this.show.activeTitle=true;
                    _this.show.normalTitle=false;
                    if(scrollTop >= changeTodetail){//吸顶详情和评价的切换
                        _this.show.titShow=2;
                    }else{
                        _this.show.titShow=1;
                    }
                    _this.isShowTopBtn = true;
                }else{
                    _this.show.activeTitle=false;
                    _this.show.normalTitle=true;
                    _this.isShowTopBtn = false;
                }
            });
        },
        //跳转店铺详情
        toShop() {
            if(this.ifNetworkError){
                this.$toast("网络异常，请稍后重试");
            }else{
                this.$router.push({ name: 'shopPage', params: { shopCode: this.lists.shopCode } });
            }
        },
        // 跳转待评价列表
        showMoreEval(){
            console.log("商品编码"+this.lists.spuCode);

            this.$router.push({name:'evaluateList',params:{ spuCode: this.lists.spuCode}});
        },
        titChange: function() {
            //点击顶部评价事件
            const titChange = this.$refs.titChange.offsetTop-60;
            this.$refs.goodsPageScroll.scrollTop=titChange;
        },
        detailChange: function() {
            //点击顶部详情事件
            const changeTodetail =this.$refs.detailChange.offsetTop-60;
            this.$refs.goodsPageScroll.scrollTop=changeTodetail;
            if(this.$refs.goodsPageScroll.scrollTop!=changeTodetail){
                this.$toast("详情已全部展示完")
            }
        },
        //打开微信分享弹窗
        wechartShare() {
            this.show.weChartShare = true;
        },
        //取消微信分享
        onCancel: function() {
            this.show.weChartShare = false;
        },
        shareAction(x){
            if (this.hasStoragePermission) {
                this.shareToFriend(x)
            }else{
                Dialog.confirm({
                    message: "请您打开存储权限，以正常使用分享功能"
                })
                    .then(() => {
                        this.requestStoragePermissions(x);
                    })
                    .catch(() => {
                        //oncancel
                    });
            }
        },
        //分享到朋友圈或者微信好友
        shareToFriend(x) {
            const data = {
                ownerCode: this.$userInfo.OWNER_CODE,
                areaCode:this.$userInfo.AREA_CODE,
                shareTo: 1,//1--分享至微信
                activitySpuId: this.activitySpuId,
            }
            getFlashSaleUrl(data).then(response => {
                if (response.resultCode == '0') {
                    this.show.weChartShare = false;
                    const shareUrl = response.data;
                    Wechat.share({
                        message: {
                            title: this.lists.spuName,
                            description: this.lists.spuSalePoint==null?'更多精彩，尽在信我家':this.lists.spuSalePoint,
                            thumb: this.lists.skuList[0].skuPicUrl,
                            media: {
                                type: Wechat.Type.WEBPAGE,
                                webpageUrl: shareUrl
                            }
                        },
                        scene: x == 1 ? Wechat.Scene.SESSION : Wechat.Scene.TIMELINE  //1是微信好友 2是朋友圈
                    }, function () {
                        //alert("Success");
                    }, reason=>{
                        this.$toast(reason);//无微信时提示用户未安装微信
                    });
                } else {
                    this.$toast("分享失败，请稍后重试");
                }
            });
        },
        checkStoragePermissions(x) {
            const platform = navigator.userAgent;
            const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            let _this = this;
            if (isiOS) {
                this.hasStoragePermission = true;
                this.shareAction(x);
            } else {
                var list = "android.permission.WRITE_EXTERNAL_STORAGE";
                var permissions = cordova.plugins.permissions;
                permissions.checkPermission(
                    list,
                    function(status) {
                        if (status.hasPermission) {
                            _this.hasStoragePermission = true;
                            _this.shareAction(x);
                        } else {
                            _this.hasStoragePermission = false;
                            _this.shareAction(x);
                        }
                    },
                    function() {
                        _this.hasStoragePermission = false;
                        _this.shareAction(x);
                    }
                );
            }
        },
        requestStoragePermissions(x) {
            const platform = navigator.userAgent;
            const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            let _this = this;
            if (isiOS) {
                this.hasStoragePermission = true;
                this.shareAction(x);
            } else {
                var list = ["android.permission.WRITE_EXTERNAL_STORAGE"];
                var permissions = cordova.plugins.permissions;
                permissions.requestPermissions(
                    list,
                    function(status) {
                        if (status.hasPermission) {
                            _this.hasStoragePermission = true;
                            _this.shareAction(x);
                        } else {
                            _this.hasStoragePermission = false;
                        }
                    },
                    function() {
                        _this.hasStoragePermission = false;
                    }
                );
            }
        },
        callShop: function() {
            const platform = navigator.userAgent;
            const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            if (isiOS) {
                window.location.href = "tel:" + this.lists.shopTel;
            } else {
                Dialog.confirm({
                    message: this.lists.shopTel,
                }).then(() => {
                    window.location.href = "tel:" + this.lists.shopTel;
                }).catch(() => {
                    // on cancel
                    Dialog.close;
                });
            }
        },
        addCollection() {//点击收藏切换收藏样式
            const _this = this;
            const savecollect = {
                collectType: '01',
                contentCode: _this.lists.spuCode,
                userCode: _this.$userInfo.OWNER_CODE,
            };
            const delectcollect = { collectCodes: [_this.collectCode] };
            if (_this.collectCode == '') {
                addToCollection(savecollect).then(function (response) {
                    _this.collectCode = response.data.collectCode;
                    _this.$toast('添加收藏成功');
                    _this.show.afterCollect = true;
                    _this.show.beforeCollect = false;
                }).catch(error => {
                    _this.$toast("网络异常，请稍后重试");
                });
            } else if (_this.collectCode != '') {
                delectCollection(delectcollect).then(function (response) {
                    _this.collectCode = '';
                    _this.$toast('取消收藏成功');
                    _this.show.afterCollect = false;
                    _this.show.beforeCollect = true;
                }).catch(error => {
                    _this.$toast("网络异常，请稍后重试");
                });;
            }
        },

        ifCollection() {
            const _this = this;
            const data = {
                collectType: '01',
                userCode: _this.$userInfo.OWNER_CODE,
                contentCode: this.spuCode
            }
            ifCollection(data).then(function (response) {
                const collectList = response.data.list;
                if (collectList.length > 0) {
                    _this.show.afterCollect = true;
                    _this.show.beforeCollect = false;
                    _this.collectCode = collectList[0].collectCode;
                } else {
                    _this.show.afterCollect = false;
                    _this.show.beforeCollect = true;
                    _this.collectCode = '';
                }
            });
        },

        onClickLeft: function() {
            this.$router.go(-1); //返回上一层
        },
        // 点击已下架商品弹窗
        goodsDown() {
            Dialog.alert({
                message: "该商品的秒杀已结束<br>下次再来吧",
                confirmButtonText: "我知道了"
            }).then(() => {

            }).catch(() => {
                // on cancel
                Dialog.close;
            });
        },
        // 补货中
        saleOut() {

            Dialog.alert({
                message: "补货中/已下架的商品<br>不可以结算哦",
                confirmButtonText: "我知道了"
            }).then(() => {
            })
        },
        //订阅
        remindSale(time,spuId) {
            const data={
                startTime:time,
                ownerCode:this.$userInfo.OWNER_CODE,
                activitySpuId:this.activitySpuId,
                subscribeFlag: this.flashSaleData.isSubsribe==0?'1':'0',
            }
            remindUser(data).then(response =>{

                if(response.resultCode=='0'&& this.flashSaleData.isSubsribe==0){

                    Dialog.alert({
                        message: "订阅成功<br>将在开抢前提醒您",
                        confirmButtonText: "我知道了"
                    }).then(() => {
                    })
                    this.remind="取消订阅"
                    this.flashSaleData.isSubsribe=1;
                }else if(response.resultCode=='0'&& this.flashSaleData.isSubsribe==1){
                    this.$toast("取消订阅成功");
                    this.remind="开抢时提醒我"
                    this.flashSaleData.isSubsribe=0;
                }else{
                    this.$toast("订阅失败,请稍后重试!");
                }
            });
        },
        // 活动开始前5分钟提醒业主
        remindBefor(item,remind){
            const _this=this;
            const data={
                startTime:item.startTime,
                ownerCode:this.$userInfo.OWNER_CODE,
                activitySpuId:item.activitySpuId,
                subscribeFlag: remind,
            }
            remindUser(data).then(response =>{
                if(response.resultCode=='0'&&item.isSubsribe==0){
                    _this.$toast("订阅成功");
                    _this.onRefresh();
                }else if(response.resultCode=='0'&&item.isSubsribe==1){
                    _this.$toast("取消订阅成功");
                    _this.onRefresh();
                }else{
                    _this.$toast("订阅失败,请稍后重试!");
                    console.log("222222");
                }
            }).catch(error => {
                _this.$toast("网络异常，请稍后重试");
            });
        },
        // 增加商品数量超过限制
        addNumBtn(){
            // if(this.flashSaleData.limitAccount==1){
            //     this.$toast("该商品限购1件哦")
            // }
            // else if(this.flashSaleData.limitAccount!=1){
            //     this.$toast("已经是极限了")
            // }
        },

        addToChart() {//添加购物车
            if (this.skuList.storeNumber != 0||this.flashSaleData.skStoreBal!=0) {
                const chartData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    spuCode: this.lists.spuCode,
                    skuNo: this.lists.skuList[0].skuNo,
                    shopCode: this.lists.shopCode,
                    skuCount: this.goodsNum,
                    activitySpuId: this.activitySpuId,
                    mark: 1
                };
                addToCart(chartData).then(response =>{
                    if (response.resultCode == '0') {
                        this.$toast('添加购物车成功');
                        this.getCartNum();
                    } else if (response.resultCode == '20100' && response.data.result == '0') {
                        this.$toast('该商品添加购物车的数量已达上限');
                    }
                });
            } else {
                this.$toast("该商品正在努力补货中哦");
            }
        },
        getCartNum() {
            const _this = this;
            const data = {
                ownerCode: _this.$userInfo.OWNER_CODE,
            }
            getCartNum(data).then(function (response) {
                _this.skuNum = response.data.skuTypeNum;
                storage.setStorage('skuTypeNum',_this.skuNum);
            });
        },
        getCount(){
            // if(this.goodsNum>=this.flashSaleData.limitAccount){
            //     this.$toast("已经是极限了")
            // }
        },
        'turnToCart': function () {
            this.$router.push({ path: '/shoppingCart' });
        },
        countDownFinish(){
            this.flashSaleDetail();
            this.init();
        },
        //键盘弹出事件
        moveInputIntoView(e){


            this.isShowPlaceHolder = true;

            this.$nextTick(() => {
                this.$refs.goodsPageScroll.scrollTop = this.$refs.changeTop.offsetTop;
            });

        },
        //键盘收回事件
        resetInputIntoView(e){
            this.isShowPlaceHolder = false;
        },
    },
    mounted: function() {
        // let goodsUrl = this.$route.query.goodsUrl;//获取列表传入的缩略图
        // if (goodsUrl && goodsUrl != undefined) {
        //     this.productImgs = [goodsUrl];
        // }
        // window.addEventListener("scroll", this.handleScroll, true);
        // this.spuCode = this.$route.query.spuCode
        // this.init();
        // this.flashSaleDetail();
    },
    computed: mapState({
        setFlashSaleSpuCode: state => state.productPage.setFlashSaleSpuCode,
    }),
    watch: {

        setFlashSaleSpuCode:{
            handler(newval, oldval) {
                console.log("setFlashSaleSpuCodenewval")
                console.log(newval,oldval)
                if(newval!=-1){
                    if(newval.indexOf(",")!=-1){
                        var arr = newval.split(",");
                        this.activitySpuId=arr[0];
                        this.spuCode=arr[1];
                        this.flashSaleDetail();
                        this.init();
                    }
                    
                    this.$store.commit("setFlashSaleSpuCodeChange", {
                        flashSaleSpuCode: -1
                    });
                }
            },
        },
    },

    activated(){
        this.isShowPreview = false;
        let goodsUrl = this.$route.params.goodsUrl;
        if (goodsUrl && goodsUrl != undefined) {
            this.productImgs = [goodsUrl];
        }
        window.addEventListener("scroll", this.handleScroll);
        //添加键盘监听,让第一响应者滚动到键盘上方显示
        window.addEventListener("native.keyboardshow", this.moveInputIntoView);
        window.addEventListener("native.keyboardhide", this.resetInputIntoView);
        document.querySelector(".goodsDetail").addEventListener('touchmove', function (e) {
            if (document.activeElement)
                document.activeElement.blur();
        }, { passive: false });
        if(this.$route.query.spuCode){
            // console.log(111111111);
            this.$refs.goodsPageScroll.scrollTop = this.scroll = 0;
            this.spuCode=this.$route.query.spuCode;
            this.activitySpuId=this.$route.query.activitySpuId;
            this.init();
            this.ifCollection();
            this.getCartNum();
            this.flashSaleDetail();
        }else{
            this.init();
            this.ifCollection();
            this.getCartNum();
            this.flashSaleDetail();
        }
    },
    deactivated(){
        this.lists={};
        this.skuList={};
        this.goodsNum=1;
        this.productImgs=[];
        this.shopInfo={};
        this.titShow=1;
        this.show.activeTitle = false;
        this.show.normalTitle = true;
        this.show.beforeCollect = true;
        this.show.afterCollect = false;
        this.isShowTopBtn = false;
       
    },

    //移除滚动条监听
    beforeRouteLeave(to, from, next){
        window.removeEventListener("scroll", this.handleScroll, true);
        next();
    }
};
