<template>
  <div>
   <van-count-down :time="timeBox" @finish="countDownFinish">
        <template v-slot="timeData" class="seckillDate">
          <span
            class="item"
            :style="{'background':'#F5A623'}"
          >{{ timeData.hours}}</span>
          <span :style="{'color':'#F5A623'}">:</span>
          <span
            class="item"
           :style="{'background':'#F5A623'}"
          >{{ timeData.minutes}}</span>
          <span :style="{'color':'#F5A623'}">:</span>
          <span
            class="item"
           :style="{'background':'#F5A623'}"
          >{{ timeData.seconds}}</span>
        </template>
      </van-count-down>
  </div>
</template>
<style scoped>
.seckillDate {
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #ffffff;
  letter-spacing: -0.04px;
}
.item {
  display: inline-block;
  width: 26px;
  height: 26px;
  margin: 0 5px;
  color: #fff;
  font-size: 12px;
  text-align: center;
  line-height: 26px;
  border-radius: 2px;
}
</style>
<script>
export default {
    name:"detailCountdown",
    props:['timeBox'],
    data:{
        returun:{
          interValTime:null,
        }
    },
    methods:{
         countDownFinish(){
             console.log("走到了这里！！");
          this.$parent.flashSaleDetail();
        }
    },
    mounted(){
        let timeDifference=this.timeBox;
        this.interValTime = setInterval(() => {
            timeDifference -= 1000;
            if(timeDifference==300000){
            this.countDownFinish();
          }
        }, 1000);
    },
    destroyed(){
      clearInterval(this.interValTime)
    }
}
</script>
